/** All interations with the contracts */
import { Accounts, Contracts } from './index';
 
async function harvestMyCatnipRewards(){
    let instance = await Contracts.getCatnipContractInstance();
    let currentAccount = await Accounts.getCurrentAccount();
    instance.methods.getReward().send({
        from: currentAccount
    });
}

/**
 * amount of NYAN to withdraw from the staked contract.
 * @param {String} amountToWithdraw 
 */
const claimDarkNyanRewards = async (amount) => {
    let account = await Accounts.getCurrentAccount();
    let instance = await Contracts.getDarkNyanContractInstance();
    
    try {
        let unstakeRes = await instance.methods.getReward().send({
            from: account
        });
    
        if (unstakeRes["status"]) {
            return {
                success: true
            }
        } else {
            return {
                success: false
            }
        }
    } catch (error) {
        console.log(error);
    }
  }


// Export each function
export {
    harvestMyCatnipRewards,
    claimDarkNyanRewards
 };