import React, {useEffect, useState} from "react";
import { Link } from 'react-router-dom';
import { Row, Col, Image, Nav, Navbar, Button } from 'react-bootstrap';
import Catlogo from '../assets/logos/cat-logo.png';

import { useGlobal } from 'reactn';

import '../style/Header.css';


const Header = (props) => {
    
    const [ web3Instance ] = useGlobal('web3Instance');
    const [ requestConnectionToWallet ] = useGlobal('requestConnectionToWallet');
    const [ menuButtonsEnabled, setMenuButtonsEnabled ] = useState(false);
    const [ isLGEOver, setIsLGEOver] = useState(false);

    useEffect(() => {

            // only fires whenever requestConnectionToWallet is updated.
            async function setMenuButtons() {
                //setIsLGEOver(await Swap.getLGEBool());

                console.log('setmenubuttons')
                if(web3Instance) setMenuButtonsEnabled(true);

            }

        setMenuButtons()
      }, [web3Instance])

    return (
        <Row>
            <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" className="header">
                <Navbar.Brand href="/">
                    <div className="logo-container">
                        <Image src={Catlogo} className="logo-image"></Image>
                        <span className="logo-text">NYAN.FINANCE</span>
                    </div>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mr-auto">
                    </Nav>
                    <Nav className="justify-content-center align-items-center">
                        <Link to="/" className="link menu">Home</Link>

                        {/*web3Instance ? <Link to="/app" className="link menu">Fund management</Link> : null*/}
                        {web3Instance && !isLGEOver? <Link to="/app/lge" className="link menu">LGE</Link> : null }
                        {web3Instance && isLGEOver? <Link to="/app/swap" className="link menu">Swap</Link> : null }

                        {/*web3Instance ? <Link to="/app/pool" className="link menu">Pool info</Link> : null }
                        {menuButtonsEnabled ? <Link to="/app/governance/voting" className="link menu">Governance voting</Link> : null }
                        {<Link to="/learnmore" className="link menu">Learn more</Link>}
                        {web3Instance ? <Link to="/app/account"><Button className="pink-menu-button">Account</Button></Link> : null */}
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </Row>
        
    )
}

export default Header;


